.login {
  display: flex;
  flex-direction: column;
  justify-content: center;

  max-width: 360px;

  margin: 0 auto;
  padding: 60px 0;
}

.login_page {
  background-color: var(--login-page--background-color);

  display: flex;
  align-items: center;
  justify-content: center;
}

.pass-reset {
  margin-top: 20px;
}

@media (max-width: 900px) {
  .login {
    padding-left: 24px;
    padding-right: 24px;
  }
}

.login__label {
  display: block;
  margin: 0;

  font-weight: 500;
  font-size: 0.9rem;
  line-height: 1.4;
  letter-spacing: 1.4px;
  text-transform: uppercase;
}

* + .login__label {
  margin-top: 1.5rem;
}

.login__input {
  display: block;
  width: 100%;
  height: 60px;
  min-width: 280px;
  padding: 0.375rem 1rem;
  margin: 0;

  font-size: 1rem;
  color: var(--login--input--color);

  background-color: var(--login--input--background-color);
  border: 1px solid var(--login--input--border-color);
  border-radius: 5px;
  outline: none;

  transition: border-color 0.15s ease-in-out;
}

* + .login__input {
  margin-top: 0.5rem;
}

.login__input:focus {
  border-color: var(--login--input--focus--border-color);
}

.login__button {
  display: block;
  padding: 1rem 1rem;

  color: var(--login--button--color);
  font-weight: 600;
  font-size: 17px;

  cursor: pointer;
  background-color: var(--login--button--background-color);
  border-radius: 5px;

  transition: background-color 0.15s ease-in-out;
}

* + .login__button {
  margin-top: 1.5rem;
}

.login__button:hover {
  background-color: var(--login--button--hover--background-color);
}

.login__button:active {
  background-color: var(--login--button--active--background-color);
}

.control {
  display: block;
  width: 100%;

  padding: 0.375rem 0.75rem;

  font-size: 1rem;
  line-height: 1.5;

  color: var(--control--color);
  background-color: var(--control--background-color);
  background-clip: padding-box;
  border: 1px solid var(--control--border-color);
  border-radius: 0.25rem;

  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
