.register {
  display: flex;
  align-items: center;
  justify-content: center;

  background: var(--register--background-color);
}

.register form,
.login {
  box-sizing: border-box;

  max-width: 800px;

  margin: 60px 0;
  padding: 2em;

  background: var(--register--form--background-color);
  border: 1px solid var(--register--form--border-color);
  border-radius: 10px;

  /* Shadow */
  box-shadow: 0 24px 64px var(--register--form--box-shadow-color);
}

.groups {
  display: flex;
  flex-wrap: wrap;
}

.register form h1 {
  font-size: 39px;
  line-height: 58px;
}

.groups > div {
  flex: 0 50%;
}

@media screen and (max-width: 600px) {
  .groups > div {
    flex: 0 100%;
  }

  .register form {
    margin: 20px 10px 10px;
    padding: 1em;
  }
}

.group {
  margin-bottom: 1rem;
  padding: 0.1rem;
}

.group input {
  height: 60px;
  padding: 0.375rem 1rem;

  transition: border-color 0.15s ease-in-out;
}

.group input:focus {
  border-color: var(--group--border-color);
}

.register label {
  display: inline-block;
  margin-bottom: 0.5rem;

  font-weight: 500;
  font-size: 14px;

  font-family: "Poppins", sans-serif;
  font-style: normal;
  line-height: 20px;

  letter-spacing: 1.4px;
  text-transform: uppercase;
}

.register form .btn {
  width: 100%;
  height: 49px;
  margin-bottom: 15px;

  color: var(--register--form--button--color);
  font-weight: 600;
  font-size: 19px;

  font-family: "Poppins", sans-serif;
  font-style: normal;
  line-height: 20px;
  text-align: center;

  background-color: var(--register--form--button--background-color);
  border-radius: 5px;
}

.register p {
  width: 100%;

  color: var(--register--text--color);

  font-weight: normal;
  font-size: 17px;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  line-height: 20px;
  text-align: center;
}

.register a {
  color: var(--register--link--color);
}

.control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;

  color: var(--control--color);

  font-size: 1rem;
  line-height: 1.5;

  background-color: var(--control--background-color);
  background-clip: padding-box;
  border: 1px solid var(--control--border-color);
  border-radius: 0.25rem;

  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.btn:disabled {
  color: var(--button--disabled--color);

  background-color:  var(--button--disabled--background-color);
  opacity: 0.2;
}
